.wrap {
  position: relative;
  overflow-x: scroll;
  width: 100%;
  height: 100%;
}

.wrap:before {
  content: " ";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.03;
  background-image: url("static.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.cover {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;

  .spine {
    background-color: #f0ead6;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    &__pageNumber {
      flex: 1;
      display: flex;
      padding: 0.5rem;
      align-items: flex-start;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;

      p {
        font-family: Montserrat;
        font-size: 1.5rem;
        padding: 0.5rem;
        margin: 0;
      }
    }

    &__meta {
      flex: 15;
    }

    &__title {
      font-family: Alegraya;
      color: #0b0e10;
      font-size: 2rem;
      text-align: center;
      margin: 0;
    }

    hr {
      width: 45%;
      height: 2px;
      background: black;
      border: none;
      opacity: 0.8;
    }
  }

  .subMatter {
    width: 100%;
    background-color: #0e1f09;
    color: white;

    &__contactInfo {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
    }

    &__trademark {
      display: flex;
      padding: 1rem;
      width: 100%;
      justify-content: center;

      h4 {
        line-height: 20px;
        position: absolute;
        bottom: 5px;
        left: 5px;
        padding: 0.5rem;
      }

      p {
        align-self: center;
      }
    }

    h4 {
      font-family: "Montserrat";
      text-transform: uppercase;
      margin-bottom: 0;
      letter-spacing: 2px;
    }

    p {
      font-family: "Alegraya";
      margin-bottom: 0;
    }

    p:nth-child(4n) {
      margin-bottom: 10px;
    }

    a {
      color: white;
      z-index: 999;
    }
  }
}
